
@import 'helpers/variables';
@import 'helpers/mixins';

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

body {
    font-family: $theme-base-font;
    font-size: 16px;
    line-height: 1.5;
    color: #19191b;
    background: #FFF;
  
}
.site-wrapper{
    overflow: hidden;
}

.slick-slide{
    &:focus{
        border:none;
        outline:none;
    }
}
h1,
h2,
h3,
h4,
h5,
h6 {
    padding: 0;
    margin: 0;
    // color: $text-dark;
}


a,
span {
    display: inline-block;
}

a {
    transition: 0.4s;
    color: inherit;
    &:focus {
        outline: none !important;
    }
    &:hover {
        text-decoration: none;
        // color: $primary-color;
    }
    
    &:visited {
        text-decoration: none;
    }
    
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type="number"] {
    -moz-appearance: textfield;
}
select {
    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 1px;
    text-overflow: '';
}
ul {
    list-style: none;
    margin: 0;
}

img {
    max-width: 100%;
}

p {
    font-size: 16px;
}
// span{
//     font-size: 13px;
// }

.btn:focus,
.btn:active {
    box-shadow: none;
}

button {
    cursor: pointer;
    background: transparent;
    border: none;
    &:focus {
        outline: none;
    }
}

select {
  /* for Firefox */
  -moz-appearance: none;
  /* for Chrome */
  -webkit-appearance: none;
}

/* For IE10 */
select::-ms-expand {
  display: none;
}


input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type="number"] {
    -moz-appearance: textfield;
}

.form-control{
    &:focus{
        box-shadow: none;
    }
}

/* Common Classes */

.section-padding{
    padding-top: 50px;
    padding-bottom: 50px;
    @include tablet {
        padding-top: 80px;
        padding-bottom: 80px;
    }
    @include desktops {
        padding-top: 120px;
        padding-bottom: 120px;
    }
}

/* Background Image */
.bg-image{
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}


/* Flex */

.flex-all-center{
    display:flex;
    justify-content: center;
    align-items: center;
}

.fs-inherit{
    font-size: inherit;
}

/* Animation */

/* Go from zero to full opacity */
@keyframes fadeEffect {
from {opacity: 0;}
to {opacity: 1;}
}

@keyframes fadeInRight {
    from {
      opacity: 0;
    margin-right:-100px; 
    }
  
    to {
      opacity: 1;
      margin-right:0px;
    }
  }
  
  .fadeInRight {
    animation-name: fadeInRight;
  }


.d-xs-block{
    @include mobile{
        display: block!important;
    }
    @include mobile-lg{
        display: block;
    }
}
.d-lg-block{
    @include desktops{
        display: block!important;
    }
}
.d-xl-block{
    @include large-desktops{
        display: block!important;
    }
}
.d-xxl-block{
    @include large-desktops-mid{
        display: block!important;
    }
}
.d-xs-none{
    @include mobile{
        display: none!important;
    }
    @include mobile-lg{
        display: none;
    }
}
.d-lg-none{
    @include desktops{
        display: none!important;
    }
}
.d-xl-none{
    @include large-desktops{
        display: none!important;
    }
}
.d-xxl-none{
    @include large-desktops-mid{
        display: none!important;
    }
}

.flex-lg-right{
    @include desktops{
        display: flex;
        justify-content: flex-end;
    }
}
.flex-md-right{
   @include tablet{
    display: flex;
    justify-content: flex-end;
   }
}
.flex-sm-right{
   @include mobile-lg{
    display: flex;
    justify-content: flex-end;
   }
}




